<script>
export default {
  name: "MyBodySize",
  components: {},
  data() {
    return {
      bodyInfo: {
        Shoulder: null,
        Chest: null,
        Waist: null,
        Arm: null,
        Leg: null,
        Thigh: null,
      },
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    goMenu(dest) {
      this.$router.push({
        name: dest,
      });
    },
    sendBodySize() {
      this.$apiPOST("/api/set/bodytype", this.bodyInfo).then(() => {
        alert(
          "체형을 작성해 주셔서 감사합니다!\n유아더 운영팀에서 체형 확인 후 체형 분류하여\n설정드릴 수 있도록 하겠습니다!"
        );
        this.goMenu("Home");
      });
    },
  },
};
</script>
